import React, { useState } from "react"
import { Fade } from "react-awesome-reveal"
import styled from "styled-components"
import SectionTitle from "../SectionTitle"
import Tab from "../ExperienceTab"

export default function Experiences() {
	const [activeTab, setActiveTab] = useState("tab3")

	const handleTab = (tab) => {
		setActiveTab(tab)
	}

	return (
		<ExperienceContainer id="experience">
			<Fade direction="up" triggerOnce duration={800}>
				<SectionTitle text="Where i've worked" data="02." />

				<ContentContainer>
					<TabNav>
						<li
							className={activeTab === "tab3" ? "active" : ""}
							onClick={() => handleTab("tab3")}
						>
							Paragon Toronto <span />
						</li>
						<li
							className={activeTab === "tab1" ? "active" : ""}
							onClick={() => handleTab("tab1")}
						>
							Janes Food
							<span />
						</li>
						<li
							className={activeTab === "tab2" ? "active" : ""}
							onClick={() => handleTab("tab2")}
						>
							ITS Staffing Solutions <span />
						</li>
					</TabNav>

					<Tabs>
						{activeTab === "tab3" && (
							<Fade>
								<Tab
									position="Security Guard"
									placeOfWork="Paragon Toronto"
									dateStart="2021"
									// dateEnd="Present"
									link="#"
									roles={[
										"Ensured a positive customer experience by greeting customers, answering questions, and providing directions for where items are located throughout the store",
										"Proactively mitigated theft by actively patrolling the retail floor.",
										"Implemented non-violent incident management techniques to confront customers who are being    disruptive, inappropriate, or acting illegally.",
										"Checks employee identification cards, maintaining an accurate record of who leaves and enters the building",
										"Completes all required paperwork at the end of each shift, including activity and incident reports.",
									]}
								/>
							</Fade>
						)}
						{activeTab === "tab1" && (
							<Fade duration={1000}>
								<Tab
									position="Food Packer"
									placeOfWork="Janes Food"
									link="#"
									dateStart="June 2018"
									dateEnd="December 2018"
									roles={[
										"Conducted daily quota for food shipments and provided product to the following department.",
										"Inspected food compressor machines and other equipment.",
										"Identified and disposed of defective items.",
									]}
								/>
							</Fade>
						)}
						{activeTab === "tab2" && (
							<Fade>
								<Tab
									position="Distributor"
									placeOfWork="ITS Staffing Solutions"
									dateStart="September 2017"
									dateEnd="May 2018"
									link="#"
									roles={[
										"Inspected daily orders of food shipment",
										"Collaborated with upper management in order to promote efficiency of food distribution.",
										"Using version control systems properly.",
										"Maintained accurate tracking reports.",
									]}
								/>
							</Fade>
						)}
					</Tabs>
				</ContentContainer>
			</Fade>
		</ExperienceContainer>
	)
}

const ExperienceContainer = styled.section`
	max-width: 700px;
	margin-left: auto;
	margin-right: auto;
`

const ContentContainer = styled.div`
	width: 100%;
	display: flex;

	@media (max-width: 600px) {
		flex-direction: column;
	}
`

const TabNav = styled.ul`
	margin-right: 1.5rem;

	li {
		position: relative;
		padding: 0 20px;
		height: 40px;
		display: flex;
		align-items: center;
		font-family: var(--font-mono);
		font-size: 0.8125rem;
		cursor: pointer;
		transition: background-color 0.25s ease, color 0.25s ease;

		&:hover {
			background-color: var(--light-navy);
			color: var(--green);
		}

		span {
			position: absolute;
			width: 2px;
			left: 0;
			top: 0;
			bottom: 0;
			background-color: var(--dark-slate);
		}
	}
	.active {
		span {
			background-color: var(--green);
		}
	}

	@media (max-width: 600px) {
		display: flex;
		flex-direction: row;
		margin-bottom: 30px;
		width: 100%;
		overflow-x: scroll;

		li {
			padding: 0 30px;

			span {
				height: 2px;
				width: 100%;
				right: 0;
				left: 0;
				top: auto;
				bottom: 0;
			}
		}
	}
`

const Tabs = styled.div`
	flex: 1;
`
