import React, { useState } from "react"
import { Fade } from "react-awesome-reveal"
import styled from "styled-components"
import SectionTitle from "../SectionTitle"
import Tab from "../ExperienceTab"

export default function Professional() {
	const [activeTab, setActiveTab] = useState("tab3")

	const handleTab = (tab) => {
		setActiveTab(tab)
	}

	return (
		<ExperienceContainer id="professional">
			<Fade direction="up" triggerOnce duration={800}>
				<SectionTitle text="Professional " data="04." />
				<ContentContainer>
					<span>
				 	<h3>Academic Work Experience</h3>
				 </span><br />
				 <span>
					<h5>Capstone Project</h5>
					</span>
				 <span>
					<h3>Project Vision</h3>
					Our vision is to create the best short-term jobs seeking website for everyone and help people find new opportunities.
				 </span><br />
				 <span id="summary">
				 	<h3>Project Summary</h3>
					<p>
						The team was formed by students of George Brown College. We are united by the idea to create an application.
					    The outcome is launching a platform for the temporary job seeking application.
						We expect to develop a platform with the basic functionality. 
						The team consists Project Manager, Developers, DevOps. 
					</p><br />
					</span>
					<span>
						<h3>Project/Business Requirements</h3>
						<p>
						 The undersigned acknowledge their agreement with the contents of Version 1.0.0 of the High-Level Requirements
						 document for Cash4Work.
                         Following approval of this document, requirements changes will be governed by the project’s change management
					     process, including impact analysis and appropriate reviews and approvals,
						 under the general control of the Project Plan and according to company policy.
						 Approved Change Request Documents, 
						 if present, will be attached to this Requirements Document as updates.
						</p><br />
					</span>
					<span>
					<h3>Project Plan</h3>
					 <p>
					   The application must have appropriate interface.
					   The application must have an efficiency filter system to prevent scam and illegal work promotion
					   Users must be able to use the application as Employee and Employers. Leave feedback and comments.
					   Application must have recommendation system to promote most relevant offers

					 </p>
					</span><br />
						<span>
						<h3>Requirements Analysis and Design</h3>
					 	<p>
						  The application must have appropriate interface.
						  The application must have an efficiency filter system to prevent scam and illegal work promotion
						  Users must be able to use the application as Employee and Employers. Leave feedback and comments.
						  Application must have recommendation system to promote most relevant offers

					 </p>
						</span>
					 
				</ContentContainer>

			</Fade>
		</ExperienceContainer>
	)
}

const ExperienceContainer = styled.section`
	max-width: 700px;
	margin-left: auto;
	margin-right: auto;
`

const ContentContainer = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;

	@media (max-width: 600px) {
		flex-direction: column;
	}
`

const TabNav = styled.ul`
	margin-right: 1.5rem;

	li {
		position: relative;
		padding: 0 20px;
		height: 40px;
		display: flex;
		align-items: center;
		font-family: var(--font-mono);
		font-size: 0.8125rem;
		cursor: pointer;
		transition: background-color 0.25s ease, color 0.25s ease;

		&:hover {
			background-color: var(--light-navy);
			color: var(--green);
		}

		span {
			position: absolute;
			width: 2px;
			left: 0;
			top: 0;
			bottom: 0;
			background-color: var(--dark-slate);
		}
	}
	.active {
		span {
			background-color: var(--green);
		}
	}

	@media (max-width: 600px) {
		display: flex;
		flex-direction: row;
		margin-bottom: 30px;
		width: 100%;
		overflow-x: scroll;

		li {
			padding: 0 30px;

			span {
				height: 2px;
				width: 100%;
				right: 0;
				left: 0;
				top: auto;
				bottom: 0;
			}
		}
	}
`

const Tabs = styled.div`
	flex: 1;
`
