import React, { useState } from "react"
import { Fade } from "react-awesome-reveal"
import styled from "styled-components"
import SectionTitle from "../SectionTitle"
// import Tab from "../ExperienceTab"

export default function Academic() {
	const [activeTab, setActiveTab] = useState("tab3")

	const handleTab = (tab) => {
		setActiveTab(tab)
	}

	return (
		<ExperienceContainer id="academic">
			<Fade direction="up" triggerOnce duration={800}>
				<SectionTitle text="Academic Work Samples " data="03." />
				<ContentContainer>
					<h3>Academic Credentials </h3>
					<p>
						Computer Business Application Specialist Diploma @ CDI COLLEGE
					</p>

					<p>
						Computer Programming Analysis Diploma @ GEORGE BROWN COLLEGE
					</p>
					<p>
						Best Student of the Month @ EMERY COLLEGIATE INSTITuTE  institute
					</p>
					<p>
						High School Diploma @ YORKDALE ADULT LEARNING CENTRE
					</p>
					
				
	
				</ContentContainer>

			</Fade>
		</ExperienceContainer>
	)
}

const ExperienceContainer = styled.section`
	max-width: 700px;
	margin-left: auto;
	margin-right: auto;
`

const ContentContainer = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;

	@media (max-width: 600px) {
		flex-direction: column;
	}
`

const TabNav = styled.ul`
	margin-right: 1.5rem;

	li {
		position: relative;
		padding: 0 20px;
		height: 40px;
		display: flex;
		align-items: center;
		font-family: var(--font-mono);
		font-size: 0.8125rem;
		cursor: pointer;
		transition: background-color 0.25s ease, color 0.25s ease;

		&:hover {
			background-color: var(--light-navy);
			color: var(--green);
		}

		span {
			position: absolute;
			width: 2px;
			left: 0;
			top: 0;
			bottom: 0;
			background-color: var(--dark-slate);
		}
	}
	.active {
		span {
			background-color: var(--green);
		}
	}

	@media (max-width: 600px) {
		display: flex;
		flex-direction: row;
		margin-bottom: 30px;
		width: 100%;
		overflow-x: scroll;

		li {
			padding: 0 30px;

			span {
				height: 2px;
				width: 100%;
				right: 0;
				left: 0;
				top: auto;
				bottom: 0;
			}
		}
	}
`

const Tabs = styled.div`
	flex: 1;
`
